import React, { useState } from 'react'
import { Redirect } from "react-router-dom"
import styled from "styled-components"
import { motion } from "framer-motion"

import {
	ArticleContainerCenter,
	ArticleTileLink,
	ArticleTile,
	ArticleTileLeft,
	ArticleTileRight,
	ArticleContainerTop,
	ArticleContainerTopLeft,
	ArticleContainerTopRight,
	ArticleTileHeader,
	ArticleTileMainliner,
	ArticleTileDescription,
	ImageContainer
} from "../ContentElements"

import BackButton from "../../../components/BackButton"
import FontProvider from "../../../fonts"
import { ProjectData, ProjectDataType } from './ProjectDescriptions'

const Projects = () => {
	const projects = ProjectData.map((p: ProjectDataType) => {
		const image = p.gif ? (
			<ImageContainer>
				<iframe src={p.img} width="100%" height="100%" frameBorder="0" className="giphy-embed" allowFullScreen></iframe><p><a href="https://giphy.com/gifs/8FDhJzbSLQvhmDVeTW"></a></p>
			</ImageContainer>	 	
		) : (
			<ImageContainer>
				<img src={p.img} width="100%" height="100%" />
			</ImageContainer>
		)
		return (
			<ArticleTileLink 
				to={p.url} 
			>
				<ArticleTile>
					<ArticleTileLeft>
						<ArticleTileHeader>{p.title}</ArticleTileHeader>
						<ArticleTileMainliner>{p.subtitle}</ArticleTileMainliner>
						<ArticleTileDescription>{p.description}</ArticleTileDescription>
					</ArticleTileLeft>
					<ArticleTileRight>
						{image}
					</ArticleTileRight>
				</ArticleTile>
			</ArticleTileLink>
		)
	})

	return (
		<Container>
			<FontProvider />
			<ArticleContainerTop>
				<ArticleContainerTopLeft>
					<BackButton direction="left" />
				</ArticleContainerTopLeft>
				<ArticleContainerTopRight>
					Projects
				</ArticleContainerTopRight>
			</ArticleContainerTop>
			<ArticleContainerCenter>
				{projects}
			</ArticleContainerCenter>
		</Container>
	)
}

const ContainerAnimation = {
	in: {
		x: 0,
		transition: {
			duration: 1
		}
	},
	out: {
		x: '100vw',
		transition: {
			duration: 1
		}
	}
}

export const Container = styled(motion.div).attrs(() => ({
	initial: "out",
	animate: "in",
	exit: "out",
	variants: ContainerAnimation,
}))`
	width:100%;
	height:100%;
	display:grid;
	grid-template-rows:20% auto;
	background-color:black;
	color:white;
	overflow:auto;
`

export default Projects
